.checkout{
    display: flex;
    padding: 20px;
    background-color: white;
    height: max-content;
}

.checkout__title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}

.checkout__ad{
    width: 100%;
    margin-bottom: 10px;
}

@media screen and (max-width: 768px) {

.checkout {
    display: flex;
    padding: 20px;
    background-color: white;
    height: max-content;
    flex-direction: column;
    flex-wrap: wrap;
}
}