.footer{
    margin-top: 50px;
    background-color: #132F3E;
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    top:0;
    z-index: 100;
}

.footer__moreinfo{
    background-color: #232F3E;
    color: aliceblue;
    display: flex;
    justify-content: space-evenly;
    padding-bottom: 40px;
}

.footer__title{
    font-weight: 700;
    color: #FFF;
    font-size: 16px;
    margin: 6px 0 10px 0;
}

#footer_minilinks {
    text-decoration: none;
    color: #FFF;
    margin-bottom: 5px
}

#footer_minilinks:hover {
    color: #FFF;
    text-decoration:underline;
}

.footer__conditions{
    font-size: 12px;
    display: flex;
    background-color: #292F3E;
    color: #FFF;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

@media screen and (max-width: 768px) {
    .footer__moreinfo{
        padding: 0px 10px;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .footer__moreinfo div{
        width: 150px;
    }
}