.home{
    max-width: 1500px;
    margin-left: auto;
    margin-right: auto;
}

.home__row {
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
}

.home__image{
    margin-top: 1px;
    width: 100%;
    /* Degrade */
    mask-image: linear-gradient(to top, transparent 1%, black 30%);
    z-index: -1;
    margin-bottom: -150px;
}

.home__image2{
    width: 100%;
    /* Degrade */
    mask-image: linear-gradient(to bottom, to top, transparent 1%, black 30%);
    margin-bottom: 10px;
    margin-top: 10px;
}

@media screen and (max-width: 768px) {
    .home__image{
        margin-bottom: -80px;
    }
    .home__row{
        flex-wrap: wrap;
    }
}